<template>
    <div>
        <h1 class="display-4 text-center text-uppercase font-weight-bold titulo">listado de tareas</h1>
        <hr>
        <div class="row">
            <div class="col-lg-8 offset-lg-2">
                <div class="card mt-4">
                    <div class="card-body">
                        <div class="input-group">
                            <input type="text" v-model="tarea" class="form-control form-control-lg" placeholder="Agregar Tarea">

                            <div class="input-group-append">
                                <button v-on:click="agregarTarea()"
                                class="btn btn-success btn-lg">Agregar</button>
                            </div>
                            
                        
                        </div>
                        
                        <br>
                        <h5 v-if="listaTareas == 0">No hay tareas a realizar</h5>
                        
                            <ul class="list-group">
                                
                                <li v-for="(tarea, index) of listaTareas" :key="index"
                                class="list-group-item d-flex justify-content-between">
                                    <span v-bind:class="{'text-success' : tarea.estado}" 
                                    class="cursor" v-on:click="editarTarea(tarea, index)">
                                        <i v-bind:class="[tarea.estado ? 'fa-regular fa-circle-check' : 'fa-regular fa-circle']"></i>
                                        
                                    </span>

                                    {{tarea.nombre}}
                                    
                                    <span class="text-danger cursor" v-on:click="eliminarTareas(index)"><i class="fa-solid fa-trash-can"></i></span>
                                </li>
                                
                            </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default{
        name: 'TareaApp',
        data(){
            return{
                tarea: '',
                listaTareas:[]
            }
                

        },
        methods:{
            agregarTarea(){
                const tarea = {
                    nombre: this.tarea,
                    estado: false
                }
                this.listaTareas.push(tarea);
                this.tarea = '';
            },
            eliminarTareas(index){
                this.listaTareas.splice(index, 1)
            },
            editarTarea(tarea, index){
                this.listaTareas[index].estado = !tarea.estado;
            }
        }
    }
</script>

<style>
    .cursor{
        cursor: pointer;
    }
    .titulo{
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        font-weight: bold;
    }
</style>